<template>
    <div class="pqrsd" ref="pqrsd_container">
        <h1 class="comunicacion__title">
            Peticiones, quejas, reclamos, sugerencias y denuncias (PQRSD)
        </h1>

        <p class="comunicacion__text">
            Durante el año 2020 la situación de emergencia que se presentó en el
            país generó un gran incremento de solicitudes (PQRSD) con respecto a
            los años anteriores, toda vez que se generó en la ciudadanía la
            necesidad de resolver inquietudes jurídicas, la preocupación de
            agilizar trámites y de recibir asesoría específica de los procesos
            judiciales de conocimiento del Consejo de Estado.
        </p>
        <p class="comunicacion__text">
            No obstante, la hábil gestión y el aumento de la capacidad de
            respuesta por parte de las unidades responsables del trámite de
            PQRSD permitió asegurar, por un lado, el cumplimiento de los
            términos legales, y de otra parte, identificar las necesidades más
            recurrentes de los peticionarios, tal como se muestra a
            continuación:
        </p>

        <div class="comunicacion__grid">
            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_1.svg"
                alt="pqrsd"
            />

            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_2.svg"
                alt="pqrsd"
            />

            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_3.svg"
                alt="pqrsd"
            />

            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_4.svg"
                alt="pqrsd"
            />

            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_5.svg"
                alt="pqrsd"
            />

            <img
                class="comunicacion__imagen"
                src="/assets/svg/pqrsd_6.svg"
                alt="pqrsd"
            />
        </div>

        <p class="comunicacion__text">
            En términos generales, se promovió el mejoramiento continuo del
            servicio, ajustado a las necesidades actuales y se garantizó el
            cumplimiento de los parámetros fijados en la ley sobre el asunto,
            con el fin de proteger el derecho fundamental de petición de los
            usuarios.
        </p>

        <h1 class="comunicacion__title" style="margin-top: 20px;">
            Comparativo PQRSD 2018, 2019 y 2020
        </h1>

        <pie-chart
            v-if="show_pie"
            :style="{
                position: 'relative',
                width: '100%',
                maxWidth: '500px',
                margin: '20px 0px'
            }"
            :data="chartData"
            :options="chartOptions"
        ></pie-chart>
        <div v-show="!show_pie" ref="hiden_div" class="hiden_div"></div>
    </div>
</template>

<script>
import PieChart from "./DonutChart.js"

export default {
    name: "pqrsd",
    components: {
        PieChart
    },
    data() {
        return {
            observer: new IntersectionObserver(this.onElementObserved, {
                root: this.$root.$el,
                rootMargin: "-50px 0px -215px 0px",
                threshold: 1
            }),
            show_pie: false,
            chartOptions: {
                hoverBorderWidth: 0,
                responsive: true,
                elements: {
                    arc: {
                        borderWidth: 0
                    }
                },
                legend: {
                    labels: {
                        fontSize: 18,
                        fontColor: "#282b63ff"
                    }
                },
                pieceLabel: {
                    mode: "value",
                    precision: 1,
                    fontSize: 20,
                    fontColor: "#282b63ff"
                }
            },
            chartData: {
                hoverBackgroundColor: "white",
                hoverBorderWidth: 0,
                labels: ["2018", "2019", "2020"],
                datasets: [
                    {
                        label: "Data dos",
                        backgroundColor: ["#eb6117ff", "#fff100ff", "#8c33b6"],
                        data: [1531, 1557, 2490]
                    }
                ]
            }
        }
    },
    methods: {
        onElementObserved(entries) {
            entries.forEach(({ target, isIntersecting }) => {
                if (!isIntersecting) {
                    return
                }

                this.observer.unobserve(target)
                this.show_pie = true
            })
        }
    },
    mounted() {
        this.observer.observe(this.$refs.hiden_div)
    },
    beforeRouteLeave: function(to, from, next) {
        this.$parent.$refs.comunicacion_content.scrollTop = 0
        this.$root.$el.scrollTop = 0
        next()
    },
    beforeDestroy() {
        this.observer.disconnect()
    }
}
</script>

<style lang="scss" src="./pqrsd.scss" scoped></style>
